body {
  color: #fff;
  text-align: center;
  letter-spacing: .6px;
  background-color: #1f1f1f;
}

h1 {
  color: #b42343;
}

h2 {
  color: #aaa6c4;
}

.container {
  max-width: 801px;
  margin: 0 auto;
  padding: 20px;
}

.js-cat-image {
  width: 800px;
  height: auto;
  animation: shadow-glow 2s ease-in-out .1s;
  box-shadow: 0 0 10px #7f7a9b, 0 0 20px #fff;
}

@keyframes shadow-glow {
  0% {
    box-shadow: none;
  }

  50% {
    box-shadow: 0 0 10px #7f7a9b, 0 0 20px #fff;
  }

  100% {
    box-shadow: 0 0 10px #7f7a9b, 0 0 20px #fff;
  }
}

.js-cat-name {
  color: #7f7a9b;
  margin-bottom: 10px;
  font-size: 24px;
}

.js-cat-text {
  color: #7f7a9b;
  max-width: 800px;
  margin-top: 5px;
  font-size: 16px;
}

.error {
  color: #aaa6c4;
}

